import './App.css';
import React, { useContext, useEffect } from 'react';
import Listings from './Listings';
// import Navbar from './Navbar-Drawer';
import { Route, Routes, useParams } from 'react-router-dom';
import Show from './Show';
import Login from './UserLogIn';
import Register from './UserRegister'
import Home from './Home';
import { ListingContext } from './contexts/ListingContext';
import { RequireAuth, useIsAuthenticated } from 'react-auth-kit'
import { Navigate } from "react-router-dom";
import ScrollToTop from './ScrollToTop';
import Page from './Page';
import ListingModForm from './ListingModForm';
import UserProfile from './UserProfile';
import Messages from './UserMessages';
import Verify from './UserVerify';
 
function App() {
  const { isAuthor, getUser } = useContext(ListingContext)
  const isAuthenticated = useIsAuthenticated()

  useEffect(()=>{
    getUser()
  }, [])

  const EditRedirect = () => {
    const { id } = useParams();
    return <Navigate to={`/listings/${id}`} />
  }

  const ListingsRedirect = () => {
    return <Navigate to={'/listings'} />
  }

  return (
    <div className="App">
      <Page>
        <ScrollToTop />
        <Routes>
          <Route path='/' element={<Home />} />
          <Route path='/profile/:id'
            element={
              <RequireAuth loginPath={'/login'}>
                <UserProfile />
              </RequireAuth>
            } />
          <Route path='/messages/:id' element={
                        <RequireAuth loginPath={'/login'}>
                        <Messages />
                        </RequireAuth>
} />
          <Route path='/listings/new'
            element={
              <RequireAuth loginPath={'/login'}>
                <ListingModForm />
              </RequireAuth>
            } />
          <Route path='/listings' element={<Listings />} />
          <Route path='/listings/:id/edit'
            element={isAuthor ?
              <RequireAuth loginPath={'/login'}>
                <ListingModForm />
              </RequireAuth>
              :
              <EditRedirect />
            } />
          <Route path='/listings/:id' element={<Show />} />
          <Route path='/login' element={isAuthenticated() ? <ListingsRedirect /> : <Login /> } />
          <Route path='/verify/:token' element={ <Login />
          } />
          <Route path='/register' element={isAuthenticated() ? <ListingsRedirect /> : <Register />} />
        </Routes>
      </Page>
    </div>
  );
}

export default App;
