import React, { useContext, useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Avatar, Grid, TextField } from '@mui/material';
import { useNavigate, useParams } from 'react-router-dom';
import { ListingContext } from './contexts/ListingContext';
import UploadFilesService from './services/upload-files.service'
import DefaultAvatar from './Default Avatar';

export default function UserProfileForm() {
  const { updateUser, getUser, setLoading } = useContext(ListingContext)

  const { id } = useParams()

  const [username, setUsername] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState("")
  const [profilePic, setProfilePic] = useState([{ url: "" }])
  const [isEditing, setIsEditing] = useState(false)

  let navigate = useNavigate()

  useEffect(() => {
    getUser()
      .then(({ data }) => {
        setUsername(data.username)
        setEmail(data.email)
        data.image && setProfilePic([{ ...data.image }])
      })
  }, [])

  const handleUsernameChange = (event) => {
    setUsername(event.target.value)
  }

  const handlePasswordChange = (event) => {
    setPassword(event.target.value)
  }

  const handleEmailChange = (event) => {
    setEmail(event.target.value)
  }

  const handleSubmit = async (event) => {
    event.preventDefault()
    setLoading(true)
    const image = profilePic[0].data ? await UploadFilesService.upload(profilePic) : { filename: 'defaultUserImage', url: profilePic[0].tempUrl }
    let obj = {
      image: image[0]
    }
    await updateUser(id, obj)
    navigate(0)
  }

  const selectFile = (event) => {
    const file = event.target.files
    const image = [{ data: file[0], url: URL.createObjectURL(file[0]) }]
    setProfilePic(image)
  }

  return (
    <Card sx={{ width: '100%', mx: 'auto' }}>
      <CardContent>

        <form onSubmit={handleSubmit}>
          <Grid container item rowGap={4}>
            <Typography variant="h4" color="text.secondary">
              Profile
            </Typography>
            <Grid container item xs={12} justifyContent='left' width='150px' height='150px' position='relative'>

              <label htmlFor="images">
                <div className="form-group">

                  <input
                    id="images"
                    name="images"
                    style={{ display: 'none' }}
                    type="file"
                    accept="image/*"
                    onChange={selectFile}
                  />
                </div>
                <div className="form-group" style={{ height: '150px', width: '150px', borderRadius: '50%', position: 'relative' }}>

                  <Button
                    className="btn-choose"
                    variant="outlined"
                    component="div"
                    sx={{ borderRadius: '50%', p: 0, height: '150px', width: '150px', position: 'absolute', zIndex: 101251 }}>
                    {(profilePic[0].url !== '' && profilePic[0].url !==
                      "/broken-image.jpg") ?
                      <Avatar
                        alt={username}
                        src={profilePic[0].url}
                        style={{ height: '100%', width: '100%', objectFit: 'cover', p: 'none', borderRadius: '50%' }} />
                      :
                      <DefaultAvatar width='100%' height='100%' font='2.5rem' username={username} />
                    }
                  </Button>
                </div>

              </label>
            </Grid>
            <TextField
              autoFocus={true}
              id="outlined-email-input"
              label="Email"
              type="email"
              value={email}
              onChange={handleEmailChange}
              autoComplete="new-email"
              size="small"
              fullWidth
              disabled={!isEditing}
              required
              focused
            />
            <TextField
              id="outlined-username-input"
              label="Username"
              type="text"
              value={username}
              onChange={handleUsernameChange}
              autoComplete="new-username"
              size="small"
              fullWidth
              disabled={!isEditing}
              required
              focused
            />
            <TextField
              id="outlined-password-input"
              label="Password"
              type="text"
              value={password}
              onChange={handlePasswordChange}
              autoComplete="new-password"
              size="small"
              fullWidth
              disabled={!isEditing}
              required
              focused
            />

            <Button type='submit' variant='contained' sx={{ width: '100%', mx: 'auto' }}>
              Save
            </Button>
          </Grid>
        </form>

      </CardContent>
    </Card>
  );
}